/* VALIDATION */

.validation-placeholder {
	min-height: 14px;
	font-size:12.6px;
	line-height: 1;
	margin-top: 5.6px;
}

.validation-message {
	color:var(--warning);
}

.input-block.validation-error {
	border-color:var(--warning);
	background-color: var(--warning_bg);
}

.mark-circle {
	display:inline-block;
	border-radius:50%;
	width:14px;
	height:14px;
	text-align: center;
	margin-right: 4px;
	line-height: 14px;
}

span.mark-circle:not(:first-of-type) {
	margin-left: 7px;
}

.mark-orange {
	background-color:var(--warning);
	color:#fff;
	font-weight: 700;
}

.mark-orange-inv {
	background-color:transparent;
	color:var(--warning);
	font-weight: 700;
	border:1px solid var(--warning);
}

/* Step4 validation */
#confirm_validation {
	color: var(--warning);
	font-weight: 600;
}


/* TOOLTIPS */

/* default tooltip - e.g. inline (€) */
.ttip {
	display: inline-block;
	position: relative;
	border-bottom: 1px dotted var(--primary);
	cursor: pointer;
	color: var(--primary);
	&:hover {
		color: var(--secondary)
	}
}

/* input block tooltip - step1 */
.input-block .ttip {
	display: block;
	position: absolute;
	right: 0;
	width: 20px;
	height: 20px;
	border-bottom: 0;
	border-radius: 50% 0 0 50%;
	font-size: 12px;
	color: #fff;
	line-height: 20px;
	text-align: center;
	background-color: #ccc;
	top:50%;
	margin-top: -10px;
	padding-left: 3px;
	&:hover {
		background-color: var(--primary);
	}
}

/* tooltip text/content box */
.ttip .ttip_text {
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s, opacity 0.15s linear;
	background-color: var(--primary_light);
	border-radius: var(--b_radius);
	font-size: 12px;
	color: #fff;
	text-align: center;
	padding: 4px;
	position: absolute;
	z-index: 1;
}

.ttip:hover .ttip_text {
	visibility: visible;
	opacity: 1;
}

/* left tooltip */
.ttip.ttip-left .ttip_text {
	top: 10px;
	right: calc(100% + 7px);
	width: 200px;
	transform: translateY(-50%);
}

.ttip.ttip-left .ttip_text::after {
	content: " ";
	position: absolute;
	top: 50%;
	left: 100%; /* arrow right */
	margin-top: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: transparent transparent transparent var(--primary_light);
}

/* top tooltip / bottom arrow (product quick overview - step2) */
.ttip.ttip-top .ttip_text {
	bottom: calc(100% + 4px);
	left: 50%;
	min-width: 200px;
	// margin-left: -100px; /* Use half of the width (120/2 = 60), to center the tooltip */
	transform: translatex(-50%);
}

.ttip.ttip-top .ttip_text::after {
	content: " ";
	position: absolute;
	top: 100%; /* bottom arrow */
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: var(--primary_light) transparent transparent transparent;
}



