/********************************************

	FORMS - BASIC FIELDS
	general form field style & basic fields

	- form-controls (input, select, textarea)
	- text
	- select
	- multiple select
	- textarea
	- checkboxes
	- radios

********************************************/


&.skin-najpoistenie {

	$skin-slug : "skin-najpoistenie";

	select {
		background: url("@/assets/images/#{$skin-slug}/forms/field-select.svg") right top no-repeat;
	}

	input[type="checkbox"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -48px center no-repeat;
	}

	input[type="radio"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") left top no-repeat;
		&:checked {
			background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -24px top no-repeat;
		}
	}

}

&.skin-finportal {

	$skin-slug : "skin-finportal";

	select {
		background: url("@/assets/images/#{$skin-slug}/forms/field-select.svg") right top no-repeat;
	}

	input[type="checkbox"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -48px center no-repeat;
	}

	input[type="radio"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") left top no-repeat;
		&:checked {
			background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -24px top no-repeat;
		}
	}

}



/*	FORM-CONTROLs - Textual form controls like <input>s, <select>s, and <textarea>s */

.form-control {
	border: 1px solid #ccc;
	color: var(--text);
	background-color: #fff;
	border-radius: var(--b_radius);
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background-clip: border-box;
	height: 36px;
	padding:.5rem .75rem;
	font-size: 1em;
}

.form-control[readonly]:focus {
	border: 1px solid #ccc;
}

.form-control:focus {
	border-color: var(--primary);
	-webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
	box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
}

input:focus,
select:focus,
input[type="text"]:focus {
	border: 1px solid var(--primary);
}

.form-control[readonly],
.form-control:disabled,
input:disabled {
	background-color: transparent;
}

input.form-control-plaintext {
	border: 1px solid transparent;
	color: var(--text);
}

input.form-control-plaintext:focus {
	border: 1px solid transparent;
}


/* SELECT */

select {
	overflow: hidden;
	// background: skin-specific
	cursor: pointer;
}

select.form-control {
	padding-right: calc(36px + .75rem); /* icon 36px + default left right padding .75 */
}

/* dropdown icon 36px + visual padding 4px */

select.form-control[multiple] {
	padding-right: 0.75rem;
	background-image: none;
}


/*	FORM-CHECK - Checkboxes & Radios */

.form-check {
	padding-top: .15rem;
	padding-bottom: .15rem
}

.form-check:hover {
	cursor: pointer;
}

.form-check:not(.form-check-inline) .form-check-input {
	margin-top: 0;
}

.form-check:not(.form-check-inline) .form-check-label {
	padding-left: 12px;
	padding-top: 1px;
}

/* konzistentny priestor s .form-check-inline .form-check-input {margin-right: .3125rem;} */


/* CHECKBOXES and RADIOS */

input[type="checkbox"],
input[type="radio"] {
	width: 24px;
	height: 24px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type="checkbox"]:hover,
input[type="radio"]:hover,
.form-check label {
	cursor: pointer;
}

input[type="checkbox"]:focus,
input[type="radio"]:focus {
	outline: 0;
	box-shadow: none;
	border-color: transparent;
}


/**** Checkbox ****/

input[type="checkbox"] {
	// background: skin-specific
}

/*.form-check {padding-left: 24px}

.form-check-input {margin-left:-24px;margin-top:0;}*/

input[type="checkbox"]:checked {
	background-position: -72px
}

input[type="checkbox"].required {
	background-position: -96px
}

input[type="checkbox"].required:checked {
	background-position: -72px
}

/**** Radio ****/

input[type="radio"] {
	// background: skin-specific
}

input[type="radio"]:checked {
	background-position: -24px
}

.input-radio input[type="radio"]:checked {
	// background: skin-specific
}
