/* INPUT GROUPS */

.input-group-text {
	border:1px solid #ccc;
	border-radius: var(--b_radius);
	background:transparent;
}


/* DATEPICKER (flatpickr.js) */

.input-datepicker input.flatpickr-input {
	display: block;
	/*height: calc(1.5em + .75rem + 2px);*/
	height:36px;
	padding: .375rem 36px .375rem .75rem; /*36px = calendar icon width*/
	font-size: 1em;
	font-weight: 400;
	line-height: 1.5;
	// background: skin-specific
	border: 1px solid #ccc;
	border-radius: var(--b_radius);
	color: var(--primary);
	text-align: center;
	width: 100%;
	&:focus {
		border-color: var(--primary);
		outline:none;
	}
}

.input-datepicker {
	display: flex;
	flex-grow: 1;
	-ms-flex-positive:1;
	flex-wrap: wrap;
	-ms-flex-wrap: wrap;
}

.flatpickr-wrapper {
	width: 100%;
}

.flatpickr-calendar {
	border-radius: 0;
}

.flatpickr-months .flatpickr-prev-month:hover svg,
.flatpickr-months .flatpickr-next-month:hover svg {
	fill: var(--primary);
}

.flatpickr-months .flatpickr-month {
	color: var(--text);
	fill: var(--text);;
}

.flatpickr-current-month {
	padding-top: 11px;
}

.flatpickr-current-month .flatpickr-monthDropdown-months {
	font-weight: 600;
	font-size: 14px;
}

.flatpickr-current-month .flatpickr-monthDropdown-months:hover {
	background: #f5f5f5;
}

.flatpickr-current-month input.cur-year {
	font-weight: 600;
}

.numInputWrapper:hover{
	background: #f5f5f5;
}

.flatpickr-current-month .numInputWrapper {
	width: 7ch;
}

.numInputWrapper span {
	background-color: #ccc;
	font-weight: 600;
	font-size: 14px;
}

.numInputWrapper span:hover {
	background-color: var(--primary_light);
}

 .flatpickr-current-month .numInputWrapper span.arrowUp:after{
	border-bottom-color: #fff;
}

.flatpickr-current-month .numInputWrapper span.arrowDown::after{
	border-top-color: #fff;
}

.flatpickr-day.selected,
.flatpickr-day.startRange,
.flatpickr-day.endRange,
.flatpickr-day.selected.inRange,
.flatpickr-day.startRange.inRange,
.flatpickr-day.endRange.inRange,
.flatpickr-day.selected:focus,
.flatpickr-day.startRange:focus,
.flatpickr-day.endRange:focus,
.flatpickr-day.selected:hover,
.flatpickr-day.startRange:hover,
.flatpickr-day.endRange:hover,
.flatpickr-day.selected.prevMonthDay,
.flatpickr-day.startRange.prevMonthDay,
.flatpickr-day.endRange.prevMonthDay,
.flatpickr-day.selected.nextMonthDay,
.flatpickr-day.startRange.nextMonthDay,
.flatpickr-day.endRange.nextMonthDay {
	background: var(--primary);
	border-color: var(--primary);
}

.flatpickr-day.inRange,
.flatpickr-day.prevMonthDay.inRange,
.flatpickr-day.nextMonthDay.inRange,
.flatpickr-day.today.inRange,
.flatpickr-day.prevMonthDay.today.inRange,
.flatpickr-day.nextMonthDay.today.inRange,
.flatpickr-day:hover,
.flatpickr-day.prevMonthDay:hover,
.flatpickr-day.nextMonthDay:hover,
.flatpickr-day:focus,
.flatpickr-day.prevMonthDay:focus,
.flatpickr-day.nextMonthDay:focus {
	background: #f5f5f5;
	border-color: #f5f5f5;
}

.flatpickr-day {
	border-radius: var(--b_radius);
}

/* date range w/text */
.input-datepicker .input-group {
	/*float:left;*/
	margin: 2px 4px 2px 0;
	width: auto;
	-ms-flex-wrap: nowrap; /*bootstrap override*/
	flex-wrap: nowrap; /*bootstrap override*/
}


/* SEARCH */

.input-search {
	border-right-width: 0;
}

.input-search-icon {
	width:36px;
	// background: skin-specific
	cursor: pointer;
	border-top-right-radius: var(--b_radius);
	border-bottom-right-radius: var(--b_radius);
}


/* FILE BROWSER */

input[type="file"] {}

.custom-file-input {
	height:36px;
}

.custom-file .custom-file-label {
	border:1px solid #ccc;
	height: 36px;
	padding:.5rem .75rem;
	border-radius: var(--b_radius);
}

.custom-file .custom-file-label::after {
	background-color:var(--primary);
	color: #fff;
	height: auto;
	padding:.5rem .75rem;
}

.custom-file-label::after {
	content: "Nahrať";
}

/* RANGE SLIDER (nouislider) */

.noUi-target {
	height: 12px;
	border: 1px solid #ccc;
	border-radius: var(--b_radius);
	box-shadow: none;
}


.noUi-horizontal .noUi-tooltip {
	color: var(--primary);
	font-weight: 700;
	font-size: 14px;
	border: 0;
	background-color: transparent;
	bottom: 100%;
}

.noUi-horizontal .noUi-handle {
	cursor: pointer;
	height: 24px;
	width: 24px;
	right: -12px;
	top: -8px;
	border-radius: 50%;
	background: var(--primary);
	border: 2px solid #fff;
	box-shadow: none;
}

.noUi-horizontal .noUi-handle:hover {
	background: var(--primary_form_light);
}

.noUi-handle::before,
.noUi-handle::after {
	display: none;
}

/* RANGE SLIDER (IRS - ion range slider) - old slider, replaced by nouislider */

.input-range-slider {
	display:block;
	width: 100%;
}

.input-group.input-range-slider {
	display: flex;
}

.irs {
	height: 36px; /*before 55px*/
}

.irs-with-grid {
	height: 75px;
}

.irs-line {
	height: 12px;
	top: 24px; /*predtym 33px */
	background: #fff;
	border: 1px solid #CCC;

}

.irs-line-left {
	height: 8px;
}

.irs-line-mid {
	height: 8px;
}

.irs-line-right {
	height: 8px;
}

.irs-bar {
	height: 12px; top: 24px; /*predtym 33px */
	border-top: 1px solid var(--primary);
	border-bottom: 1px solid var(--primary);
	background: var(--primary);
}

.irs-bar-edge {
	height: 12px; top: 24px; /*predtym 33px */
	width: 14px;
	border: 1px solid var(--primary);
	border-right: 0;
	background: var(--primary);
	/*border-radius: 16px 0 0 16px;*/
	/*-moz-border-radius: 16px 0 0 16px;*/
}

.irs-shadow {
	height: 2px; top: 29px; /*predtym 38px */
	background: #000;
	opacity: 0.3;
	border-radius: 5px;
	-moz-border-radius: 5px;
}
.lt-ie9 .irs-shadow {
	filter: alpha(opacity=30);
}

.irs-slider {
	top: 18px; /*predtym 25px */
	width: 23px; height: 23px; /*predtym 27px */
	border: 2px solid #fff;
	background: var(--primary);
	border-radius: 27px;
	-moz-border-radius: 27px;
	cursor: pointer;
}

.irs-slider.state_hover, .irs-slider:hover {
	background: var(--primary_form_light);
}

.irs-min, .irs-max {
	color: var(--text);
	font-size: 12px; line-height: 1.333;
	text-shadow: none;
	top: 0;
	padding: 1px 5px;
	border-radius: 3px;
	-moz-border-radius: 3px;
}

.lt-ie9 .irs-min, .lt-ie9 .irs-max {
	background: #ccc;
}

.irs-from, .irs-to, .irs-single {
	top:-4px;
	color: var(--primary);
	font-weight: 700;
	font-size: 14px; line-height: 1.333;
	text-shadow: none;
	padding: 1px 5px;
	/*background: #428bca;*/
	border-radius: 3px;
	-moz-border-radius: 3px;
}

.lt-ie9 .irs-from, .lt-ie9 .irs-to, .lt-ie9 .irs-single {
	background: #8d8d8d;
}

.irs-grid {
	height: 27px;
}

.irs-grid-pol {
	opacity: 0.5;
	background: #8d8d8d;
}

.irs-grid-pol.small {
	background: #8d8d8d;
}

.irs-grid-text {
	bottom: 5px;
	color: #8d8d8d;
}

.irs-disabled {
}


/* RANGE SLIDER W/ manual input*/

.input-group .irs {
	flex-grow: 1;
	-ms-flex-positive: 1;
}

.input-group .irs-manual-input {
	-ms-flex: initial;
	flex: initial;
	min-width:60px;
	margin-left: 4px;
	text-align: center;
	color: var(--primary);
	font-weight: 700;
}


/* ADVANCED SELECT (a.k.a. SELECT2 - https://select2.org/) - select w/ search */

.select2-container { /*fix for select2 + bootstrap */
	position: relative;
	z-index: 2;
	float: left;
	width: 100%;
	margin-bottom: 0;
	display: table;
	table-layout: fixed;
}

.form-control.select2 {
	width:100%; /* just to be sure (not needed since it's inline as well https://select2.org/appearance#example) - */
}

.select2-dropdown {
	border:1px solid#ccc;
}

.select2-container .select2-selection--single,
.select2-container--default .select2-selection--multiple {
	min-height:36px; /* expandable for multiple options */
}

.select2-container--default .select2-selection--single,
.select2-container--default .select2-selection--multiple {
	border:1px solid #ccc;
	border-radius: 0;
	// background: skin-specific
	outline:0;
}

.select2-container--default .select2-selection--single:focus,
.select2-container--default .select2-selection--multiple:focus {
	border: 1px solid var(--primary);
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: var(--primary);
}

.select2-container--open .select2-dropdown--below {
	margin-top: -1px;
	border: 1px solid #ccc;
	border-radius: 0 0 var(--b_radius) var(--b_radius);
}

.select2-container--default .select2-search--dropdown .select2-search__field {
	border:1px solid #ccc;
}

.select2-container--default .select2-search--dropdown .select2-search__field:hover {
	border: 1px solid var(--primary);
	transition: border-color .15s ease-in-out;
}

.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: #eee;color:#8d8d8d;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
	line-height: 36px;
	outline:0;
	padding-right:40px; /*36px icon + 4px visual padding*/
	padding-left:.75rem; /*same as other inputs, e.g. select*/
	color:var(--text);
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered {
	line-height: 25px;
	outline:0;
	padding-right:40px; /*36px icon + 4px visual padding*/
	padding-left:.75rem; /*same as other inputs, e.g. select*/
	color:var(--text);
}

.select2-container--default .select2-selection--single .select2-selection__arrow,
.select2-container--default .select2-selection--multiple .select2-selection__arrow {
	display:none;
}

/*.select2-container--default .select2-selection--single .select2-selection__rendered:focus,*/
.select2-selection-select2-selection--single:focus,
.select2-container--default .select2-selection--multiple:focus {
	outline: 0;
	box-shadow: none;
	border-color: transparent;
	border:0;}

.select2-container--focus {
	outline:none;
}

.select2-container--open .select2-dropdown--above {
	border-radius: var(--b_radius) var(--b_radius) 0 0;
}

/*multiple choice specific*/
.select2-container--default.select2-container--focus .select2-selection--multiple {
	border:1px solid #ccc;
	outline: 0;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
	border-radius:0;
	background-color: var(--primary);
	color: #fff;
	border-width: 0;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
	color:rgba(255,255,255,.5);
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove:hover {
	color:rgba(255,255,255,1);
}

.select2-container--default .select2-results__option[aria-selected=true] {}

/* moved from general.css */
.select2-selection__arrow {
	display: none;
}

/*.select2-container--default .select2-results__option--highlighted[aria-selected] {color:#8d8d8d;}
.select2-container--default .select2-results__option--highlighted[aria-selected]:hover {color:#fff;}*/


/* COUNTER (PLUS/MINUS) */

.input-counter input {
	text-align: center;
}

.input-group-btn {
	width:36px;
	height: 36px;
	cursor: pointer;
}

.input-group-prepend .input-group-btn {
	border-radius: var(--b_radius) 0 0 var(--b_radius);
}

.input-group-append .input-group-btn {
	border-radius: 0 var(--b_radius) var(--b_radius) 0;
}

.btn-minus {
	// background: skin-specific
	background-clip: content-box;
}

.btn-plus {
	// background: skin-specific
	background-clip: content-box;
}


