/* drag & drop advanced (icon, upload button etc.) */
.dropzone.dz-advanced {
	position: relative;
	color:#8d8d8d;}

.dropzone.dz-advanced .btn-grey {color:#8d8d8d;}

.dropzone.dz-advanced .dz-message .dz-button {
	font-weight: 700;
	display: block;
	padding:.7rem 0;
	width:100%;
}

.dropzone.dz-advanced img.dz-upload {
	width:40px;
}

.dropzone.dz-advanced button.dz-camera {
	position:absolute;
	width:50px;
	height:36px;
	top:10px;
	right: 10px;
	padding:5px;
	cursor: pointer;
}

.dropzone.dz-advanced button.dz-camera img.dz-camera {
	max-width: 100%;
	max-height:100%;
	cursor: pointer;
}
