/**************************************************

	DOCS LANDING PAGE STYLES

	dl_item_ = sedy blok
	dl_item_text = textovy blok
	dl_item_file = blok pre file upload (multi + single)
	dl_item_action = blok pre odoslanie do poistovne (multi)

**************************************************/

&.skin-najpoistenie {

	$skin-slug : "skin-najpoistenie";

	.dl_success .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_np.svg");
	}

	.dl_sent .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_double_np.svg");
	}

	.dl_warning .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_warning_np.svg");
	}

}

&.skin-partners {

	$skin-slug : "skin-partners";

	.dl_success .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_np.svg");
	}

	.dl_sent .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_double_np.svg");
	}

	.dl_warning .dl_status p {
		background-image: url("@/assets/images/#{$skin-slug}/results/ic_warning_np.svg");

	}

}




.docs_landing_scope {

	// change from rem to px (lukas)
	h1, .h1 { font-size: 40px; }
	h2, .h2 { font-size: 32px; }
	h3, .h3 { font-size: 28px; }
	h4, .h4 { font-size: 24px; }
	h5, .h5 { font-size: 20px; }
	h6, .h6 { font-size: 16px; }

	.dl_item_text,
	.dl_item_file,
	.dl_item_action {
		background-color: #f7f7f7;
		margin-bottom: 30px;
	}

	.dl_item_text,
	.dl_item_action {
		padding: 20px
	}

	div p:last-child {
		margin-bottom: 0;
	}

	// BASIC LAYOUT / NO GRID / SINGLE

	.dl_filename,
	.dl_up_download,
	.dl_status,
	.dl_action {
		display: flex;
	}

	.dl_status,
	.dl_action {
		background-color: #eee;
	}

	.dl_title {
		padding: 0 8px 8px; // p-2 (.5rem)

		h5 {
			color: var(--text);
			font-weight: 600;
			font-size: 14px;
			margin-bottom: 0;
		}
	}

	.dl_image {
		.img_wrapper {
			border: 1px solid #ddd;
			padding: 2px;
			background-color: #fff;
		}

		img {
			max-width: 100%;
			height: auto;
			opacity: .2;
		}
	}


	.dl_filename {
		width: 180px;
		overflow: hidden;
		text-overflow: ellipsis;
		padding: 20px 0;
	}

	.dl_up_download {
		white-space: nowrap;

		input[type="file"] {
			display: none;
		}

		button:last-child {
			margin-left: 8px
		}

	}

	.dl_progress {
		flex-basis: 8px;
		background-color: #ddd;
		position: relative;
	}

	.dl_bar {
		position: absolute;
		bottom: 0;
		left: 0;
		background-color: var(--primary);

		&.bar_vertical {
			width: 8px;
		}

		&.bar_horizontal {
			height: 8px;
		}

	}

	.dl_status {
		flex-grow: 1;
		font-weight: 600;

		p {
			display: inline-block;
		}
	}

	// STAVY

	.dl_success,
	.dl_sent {
		.dl_bar {
			background-color: var(--primary);
		}

		.dl_status {
			color: var(--primary);
		}

		.dl_image img {
			opacity: 1;
		}
	}

	.dl_success .dl_status p {
		background-repeat: no-repeat;
		padding-left: 25px; // 20 icon width + 10 space
	}

	.dl_sent .dl_status p {
		background-repeat: no-repeat;
		padding-left: 37px; // 27 icon width + 10 space
	}

	.dl_warning {

		.dl_bar {
			background-color: var(--warning);
		}

		.dl_status {
			color: var(--warning);

			p {
				background-repeat: no-repeat;
				padding-left: 30px; // 20 icon width + 10 space

			}
		}

	}


	// GRID LAYOUT / MULTI

	.tiles .dl_title {
		padding-top: 24px; // p-3 (1.5rem)
	}

	.tiles .dl_bar {
		height: 8px;
	}

	.tiles .dl_image img {
		opacity: 1;
	}

	.tiles .img_wrapper {
		@media (min-width: 767px) {
			height: 180px;
		}
		@media (min-width: 860px) {
			height: 247.5px;
		}
		overflow: hidden;
		display: flex;
		align-items: center; /* vertical */
		justify-content: center; /* horizontal */
	}

	// MOBILE

	@media (max-width: 767px) {

		.dl_filename {
			width: initial;
		}

		.dl_action {
			padding-top: 10px;
		}

		.dl_bar {
			height: 8px;
		}


	}

}
