/* SELECT + TEXT (e.g. ŠPZ) */

.input-select-text select.form-control {
	-ms-flex: 0 1 80px;
	flex: 0 1 80px; /* 40px select area + 40px dropdown icon (36px img + 4px visual padding) */
	margin-top: 0;
	margin-bottom: 0;
	border-radius: var(--b_radius) 0 0 var(--b_radius);
}

.input-select-text .form-control {
	flex: 1 1 auto;
	width: 1%;
	margin-left: -1px;
	border-radius: 0 var(--b_radius) var(--b_radius) 0;
}

.input-select-text select.form-control.select-narrow {
	-ms-flex: 0 1 62px;
	flex: 0 1 62px; /* 40px select area + 22px dropdown icon (18px img + 4px visual padding) */
	// background: skin-specific
	padding-right: 22px; /* dropdown icon 18px + visual padding 4px */
}


/* MODAL TRIGGER FIELD (e.g. modal for credentials scan) */

.input-modal-trigger input {
	color: var(--text);
}

.input-modal-trigger .btn {
	height: 36px;
	padding: .5rem .75rem;
}


/* DRAG & DROP (custom js) */

.drag-n-drop {
	border: 1px dashed #ccc;
	border-radius: var(b_radius);
	background: rgba(255,255,255,.9);
	padding: 1em;
	width: 100%;
	text-align: center;
	color: #8d8d8d;
	position: relative;
}

img.dd-upload {
	width: 40px;
}

.dd-title {
	font-weight: 700;
	padding: .7rem 0;
	margin-bottom: 0; /* moved from lukas_styles.css */
}

.dd-file {}

.dd-success:not(.hidden) {
	display: inline-block;
	margin-left: 6px;
	width: 20px;
	height: 17px;
	// background: skin-specific
}

.drag-n-drop input {
	padding: .7rem 0;
}

.input-file-button {}

.drag-n-drop input[type="file"] {
	position: absolute;
	z-index: -1;
	top: 6px;
	left: 0;
	font-size: 15px;
	color: rgb(153,153,153);
}

.drag-n-drop label.btn-grey {
	color: #8d8d8d;
	cursor: pointer;}

.drag-n-drop button.dd-camera {
	position: absolute;
	width: 50px;
	height: 36px;
	top: 10px;
	right: 10px;
	padding: 5px;
	cursor: pointer;
	z-index: 1; /* moved from lukas_styles.css */
}

.drag-n-drop button.dd-camera img.dd-camera {
	max-width: 100%;
	max-height: 100%;
	cursor: pointer;
}

#credentials_scan_response {
	color: var(--accent);
	border: 1px solid var(--accent);
	padding: .75rem;
}

.input-group-append .input-upload-icon {
	width: 36px;
	// background: skin-specific
	cursor: pointer;
	border-radius: 0 var(--b_radius) var(--b_radius) 0;
}

/* MOVED FROM lukas_styles.css */

.dd-upload-wrap {
	min-height: 70px;
}

img.dd-upload.base64 {
	width: initial;
	max-height: 70px;
}

/* scan_credentials - WebRTC Still photo capture */
.scan_video,
.scan_photo,
.scan_output,
.scan_camera,
.contentarea_video {
	width: 530px;
}

.scan_video,
.scan_photo {
	border: 1px solid black;
	height: 397px;
}

.scan_canvas {
	display:none;
}

.scan_camera,
.scan_output {
	display:inline-block;
}

.scan_button {
	display: block;
	position: relative;
	margin-left: auto;
	margin-right: auto;
	bottom: 54px;
}

/* force break lines */
.wrap_me {
	white-space: pre-wrap;			/* CSS3 */
	white-space: -moz-pre-wrap; /* Firefox */
	white-space: -pre-wrap;		 /* Opera <7 */
	white-space: -o-pre-wrap;	 /* Opera 7 */
	word-wrap: break-word;			/* IE */
}

/* / scan_credentials - WebRTC Still photo capture END */

/* / MOVED FROM from lukas_styles.css END */


/* BUTTON RADIOS / CHECKBOXES */

.input-button-icon,
.input-button-blank {
	display: flex; /* .d-flex */
	display: -ms-flexbox; /* .d-flex */
	align-items: center; /* .align-items-center */
	-ms-flex-align: center; /* .align-items-center */
	flex-wrap: wrap; /* .flex-wrap */
	-ms-flex-wrap: wrap; /* .flex-wrap */
	}

.input-button-icon,
.input-button-blank {
	margin-right: 4px; /* same spacing as vertical (form-check - padding top/bottom .15rem) when wrapped (smaller screens) */
}

.input-button-blank input[type="radio"] + label,
.input-button-blank input[type="checkbox"] + label,
.input-button-icon input[type="radio"] + label	{
	width: 150px;
	border: 1px solid var(--primary);
	border-radius: var(--b_radius);
	text-align: left;
	background: #ffffff;
	color: var(--primary);
	cursor: pointer;
	/*white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;*/
}

.input-button-blank input[type="radio"]:not(:checked) + label:hover,
.input-button-blank input[type="checkbox"]:not(:checked) + label:hover,
.input-button-icon input[type="radio"]:not(:checked) + label:hover	{
	background-color: var(--primary_bg) !important;
}

.input-button-icon input[type="radio"],
.input-button-icon input[type="checkbox"],
.input-button-blank input[type="radio"],
.input-button-blank input[type="checkbox"] {
	display: none;
}

.input-button-icon input[type="radio"]:checked + label,
.input-button-icon input[type="checkbox"]:checked + label,
.input-button-blank input[type="radio"]:checked + label,
.input-button-blank input[type="checkbox"]:checked + label {
	background: var(--primary);
	border: 1px solid var(--primary);
	color: #ffffff;
}

.input-button-icon .form-check-input:disabled~.form-check-label,
.input-button-blank .form-check-input:disabled~.form-check-label {
	border:1px solid #8d8d8d;
	cursor: not-allowed;
}


/* BUTTON RADIOS - BLANK */

.input-button-blank input[type="radio"] + label,
.input-button-blank input[type="checkbox"] + label {
	padding: .464rem 8px; /*height: 36px*/
}

.input-button-blank input + label {}

.input-button-blank input:checked + label {
	background: var(--primary);
}


/* BUTTON RADIOS - W/ ICONS + SELECT */

/* Typ vozidla 1 (PZP) */
.input-mixed { /* same properties as BS4's input-group */
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-ms-flex-align: stretch;
	align-items: stretch;
	width: 100%;
}

.input-mixed select {
	/*display: flex;*/
	flex: initial;
	width: 1%;
	padding-right: calc(36px + .75rem);
	margin-top: .15rem;
	margin-bottom: .15rem;
	-ms-flex-positive:1;
	flex-grow: 1;
}

.input-mixed .input-button-icon,
.input-mixed .input-button-blank {
	width: 100%;
	margin-right: 0;
}

.input-mixed .input-button-icon input[type="radio"] + label,
.input-mixed .input-button-icon input[type="checkbox"] + label {
	width: 100%
}


/* BUTTON RADIOS - W/ ICONS + TEXT FIELDS (SHOW / HIDE) */

.input-onoff .input-button-icon {
	width:100%;
}

.input-mixed.input-onoff .input-button-icon input[type="radio"] + label,
.input-mixed.input-onoff .input-button-icon input[type="checkbox"] + label {
	width: 100%
}

.input-onoff .form-control {
	margin:.15rem 4px .15rem 0;
	flex: 1 1 auto;
	width:1%;
}

.input-onoff .form-control:last-child {
	margin-right: 0;
}

.input-mixed .input-on-off.input-button-icon {
	width:251px;
	padding-right: .15rem}

 .input-mixed .input-on-off.input-button-icon input[type="checkbox"] + label {
		min-width: 100%;
	}

/*inputs (input-area)*/
.input-button-icon-text,
.input-group.input-on-off {
	flex-wrap:nowrap;
	-ms-flex-wrap:nowrap;
}

.input-group.input-on-off>.form-control,
.input-button-icon-text>.form-control {
	margin: .15rem 4px .15rem 0;
	width: 100%
}

/* MOVED FROM lukas_styles.css (hardcoded show_hide zp_axa) - technicka classa pre specialnu kontrolu show_hide, bude sa prerabat */
div.hidden_zp_axa {
	display: none;
}
