// @import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap');

* {
	font-family: 'Open Sans', sans-serif;
}

.container {
	max-width: 1170px;
}

div:not(h1, h2, h3, h4) {
	font-size: 14px;
	color:var(--text);
}

a {
	color: var(--primary);
	&:hover {
		color: var(--secondary);
		text-decoration:none;
	}
	&:focus {
		outline: none;
	}
}

.table {
	color:var(--text);
}

.table td,
.table th {
	border-top: 1px solid #ddd;
}

.table thead th {
	border-bottom: 1px solid #ddd;
}

.hidden {
	display: none;
}

.relative {
	position:relative;
}

ul {
	list-style-position: inside;
}

ul.nobullets {
	list-style-type: none;
}


/* headings */

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
	line-height: 1.4;
}

h1, h2 {
	margin-bottom:0.7em;
}

.d-blue, h1.d-blue, h2.d-blue, h3.d-blue, h4.d-blue, h5.d-blue, h6.d-blue, span.d-blue, strong.d-blue {
	color:var(--secondary);
}

h1.blue, h2.blue, h3.blue, h4.blue, h5.blue, h6.blue, span.blue, strong.blue {
	color:var(--primary);
}

h1.orange, h2.orange, h3.orange, h4.orange, h5.orange, h6.orange, span.orange, strong.orange {
	color:var(--accent);
}

h1.white, h2.white, h3.white, h4.white, h5.white, h6.white, span.white {
	color:#FFF;
}

.text-orange {
	color: var(--accent);
}

/* spacing (old, but needed, e.g. for OSM) */

.separator-10 {margin:0 0 10px;}
.separator-20 {margin:0 0 20px;}
.separator-30 {margin:0 0 30px;}
.separator-40 {margin:0 0 40px;}
.separator-50 {margin:0 0 50px;}
.separator-60 {margin:0 0 60px;}
.separator-70 {margin:0 0 70px;}
.separator-80 {margin:0 0 80px;}
.separator-90 {margin:0 0 90px;}
.separator-100 {margin:0 0 100px;}
.narrow {padding:20px 10px;}
.narrower {padding:20px 50px;}


/**** BUTTONS ****/

.btn,
button {
	padding: 8px 12px;
	border-radius: var(--b_radius);
	transition: background 0.5s;
	-webkit-transition: background 0.5s;
	-moz-transition: background 0.5s;
	-o-transition: background 0.5s;
	text-transform: uppercase;
	&:focus {
		outline:0;
		box-shadow: none;
	}
	&:active {
		box-shadow: none;
	}
}


/* btn sizes */

.btn-xlg {
	padding:14px 30px;
	font-size:16px;
	line-height:1.3333333;
	text-transform: uppercase;
	letter-spacing: 1px;}

.btn-lg {
	font-size:16px;
}

.btn-m {
	font-size: 14px;
	padding: 5px 12px; // btn height 33px for 14px font size
}

/* clear_session_modal.php, back-modal.php,...? */
.btn-w80 {
	min-width: 80px;
}

/* btn colors */

.btn-primary,
.btn-blue {
	color: #fff;
	background-color:var(--primary);
	border-color:var(--primary);
	&:hover,
	&:active,
	&:focus,
	&:not(:disabled):not(.disabled):active {
		color: #fff;
		background-color:var(--primary_light);
		border-color:var(--primary_light);
	}
	&:disabled {
		background-color: var(--primary);
		border-color: var(--primary);
	}
}


.btn-blue:focus {
	color:#fff;
}

.btn-orange,
.btn-accent {
	background-color: var(--accent);
	color:#fff;
	&:hover {
		background-color: var(--accent_light);
		color:#fff;
	}
}

.btn-grey,
.btn-light {
	background-color: #eee;
	color:var(--primary);
	&:hover,
	&:focus {
		background-color: #f7f7f7;
		color:var(--primary);
		border-color:transparent;
	}
}

.btn-white {
	color:var(--primary);
	background: #fff;
}

.btn-white:hover {
	color:var(--primary);
	background-color: #f7f7f7;
}

.form-pagination .btn-white,
#content_subpage .btn-white {
	border:1px solid #ddd;
}

/* btn types */
.btn-right {
	// background-image: skin-specific
	background-repeat: no-repeat;
	background-position: right 10px center;
	padding-right: 30px;
}

.btn-left {
	// background-image: skin-specific
	background-repeat: no-repeat;
	background-position: 10px center;
	padding-left: 30px;
	text-align: left;
}

.btn-send {
	background-image: url("@/assets/images/common/ic_send_24px.svg");
	background-repeat: no-repeat;
	background-position: right 10px center;
	padding-right: 43px; // 12 padding + 21 icon + 10 space
}

.btn-download {
	background-image: url("@/assets/images/common/ic_file_download_24px.svg");
}

.btn-upload {
	background-image: url("@/assets/images/common/ic_file_upload_24px.svg");
}

.btn-download,
.btn-upload {
	background-repeat: no-repeat;
	background-position: left 10px center;
	padding-left: 36px; // 12 padding + 14 icon + 10 space
	&.btn-compact {
		width: 38px;
		height: 33px;
		padding: 0;
	}
}


/**** SECTIONS ****/

section {
	padding:40px;
}

section.nomargin {
	margin-bottom:0;
}

section.nopadding {
	padding:0px;
}

/* section colors */

section.grey {
	background-color:#F7F7F7;
}

section.white {
	background-color:#ffffff;
}

section.blue {
	background-color:#268fa3;
}

section.darkblue {
	background-color:#1a4c6e;
}

section.orange {
	background-color:#f28f38;
	color:#fff;
}

.orange a.btn-white {
	color:#f28f38;
	text-decoration:none;
	font-weight: normal;
}

section.orange a {
	color:#fff;
	text-decoration: underline;
	font-weight:600;
}

section.orange a:hover {
	color: #F5F5F5;
}

