
	.vue_modal {
		/* The Modal (background) */
		display: block;
		position: fixed;
		z-index: 1;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: auto;
		background-color: rgba(0,0,0,0.4);

		/* Modal Content/Box */
		.modal-content {
			margin: 15% auto;
			padding: 20px;
			width: 80%;
			max-width: 900px;
		}

		/* The Close Button */
		.close {
			color: #aaa;
			float: right;
			font-size: 28px;
			font-weight: bold;
		}

		.close:hover,
		.close:focus {
			color: black;
			text-decoration: none;
			cursor: pointer;
		}
	}
