/********************************************

	FORMS - IMAGE BUTTONS
	radio buttons with icons/images

********************************************/


.input-button-icon input[type="radio"] + label,
.input-button-icon input[type="checkbox"] + label {
	padding: .464rem 8px .464rem 42px; /* 42px icon space, 36px height	*/
	width: 150px;
	border: 1px solid var(--primary);
	border-radius: var(--b_radius);
	text-align: left;
	color: var(--primary);
	cursor: pointer;
}

.input-button-icon input[type="radio"] + label{	}

.input-button-icon.h-large input[type="radio"] + label {
	height: 50px;
}

.input-button-icon.h-large label {
	float: left;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;

	-webkit-flex-pack: center;
	-webkit-justify-content: center;
	-moz-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;

	-webkit-flex-direction: column;
	-moz-flex-direction: column;
	-ms-flex-direction: column;
	flex-direction: column;
}


