<template>
	<div class="input-group">
		<input type="text" class="form-control input-search" :id="field_id" :name="field_id" :placeholder="placeholder" v-model="value" @keyup="run_search">
		<div class="input-group-append">
			<span class="input-search-icon" @click="run_search"></span>
		</div>
		<span v-show="error_message" class="search_input_error">{{ error_message }}</span>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';
	import Search_requests from '@/logic/requests/Search_requests.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				error_message: '',
			}
		},
		methods: {
			run_search(event)
			{
				if (event.type === 'keyup' && event.keyCode !== 13) {
					return;
				}
				// validation
				let search_type = '';
				if (Functions.ends_with(this.field_id, 'company_search'))
				{
					search_type = 'ico';
					if (this.value.length !== 8 || !Functions.contains_only_digits(this.value)) {
						this.create_search_error(search_type);
						return;
					}
				}
				else if (Functions.ends_with(this.field_id, 'auto_search'))
				{
					if (this.value.length === 7) {
						search_type = 'spz';
					} else if (this.value.length === 17) {
						search_type = 'vin';
					} else {
						this.create_search_error('spz_vin');
						return;
					}
				}
				else {
					console.log('running on wrong ID');
					return;
				}
				// request and fill data
				Promise.resolve().then(
					() => {
						if (search_type === 'spz' || search_type === 'vin') {
							return Search_requests.car_data_search(search_type, this.value);
						} else if (search_type === 'ico') {
							return Search_requests.ico_info(this.value);
						}
					}
				).then(
					(response) => {
						if (response.status !== 200 || response.result.data.length === 0) {
							this.create_search_error('common');
							return;
						}
						State.save_multiple_user_inputs(response.result.data);
					}
				);
			},
			create_search_error(search_type)
			{
				let messages = {
					spz_vin: 'Zadajte v tvare BA123XY alebo TMBHG41U642123456',
					ico: 'Zadajte 8 miestne číslo.',
					common: 'Vyhľadávanie nebolo úspešné.'
				}
				this.error_message = messages[search_type];
				setTimeout(
					() => { this.error_message = '' },
					2400
				);
			},
		},
		computed: {
			placeholder()
			{
				return this.field_data.placeholder;
			},
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set(event_value) {
					event_value = event_value.toUpperCase();
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
	}
</script>

<style>
	.search_input_error {
		margin-left: 15px;
		display: flex;
		justify-content: center;
		align-content: center;
		flex-direction: column;
	}
</style>
