&.skin-partners {

	/*

	partners red: #AB0534; RGB 171, 5, 52; {rgba(171,5,52,0.85);}
	partners red hover (85%): #B72A52;
	neutral bg:  #f9f9f9;		// BEFORE:  partners red bg (2%): #FEFAFB;
	neutral bg border: #ddd;	// BEFORE:  partners red bg border: #E6B3C1; (partners red 30%)
	partners dark grey: #4d4d4d;
	partners light red: #CD6D80;
	dark red: #770324; (partners red shade +3)
	yellow orange (by bluekiwi): #F8942C; {rgba(248,148,44,0.85);}
	yellow orange bg: #FFF4E9;

	Partners design manual:
	https://partners.milk.sk#colour

	*/

	/**** Colors ***/

	--text: #4d4d4d; /* partners dark grey grey (matterhorn) */
	/* default font color */

	--primary: #AB0534; /* partners red */
	/* default primary color - links, buttons, some headings, nav items, horizontal lines... */

	--primary_light: rgba(171,5,52,0.85); /* partners red .85 */
	/* lighter primary color - hover for blue bg elements (e.g. buttons) */

	--primary_bg: #f9f9f9; /* partners red bg */
	/* background for products on results page */

	--primary_bgdark: #ddd; /* partners red bg border */
	/* borders & lines for light backgrounds (products on results page) */

	--secondary: #770324; /* dark red */
	/* link hovers, headings for content pages, etc. */

	--accent:  #AB0534; /* partners red */
	/* default accent color - orange elements (e.g. buttons) */

	--accent_light: rgba(171,5,52,0.85); /* partners red .85 */
	/* lighter accent color - hover for orange bg elements (e.g. buttons) */

	--accent_bg: #FFF4E9; /* red bg */
	/* just in case, not being used at this time */

	--warning: #F8942C; /* yellow orange */
	/* warnings, validation, error messages */

	--warning_light: rgba(248,148,44,0.85); /* yellow orange .85 */
	/* hover for warnings, validation, error messages */

	--warning_bg: #FFF4E9; /* yellow orange bg */
	/* background for error messages */

	--light: #f7f7f7; /* lightest grey  (lynx white) */
	/* form fields input blocks bg... */

	--primary_form: #AB0534; /* partners red */
	/* form fields primary color */

	--primary_form_light: #B72A52; /* partners red hover */
	/* form fields light primary color - form element hovers */

	/**** /Colors END ****/



	/**** Design ***/

	--b_radius: 4px;

	/**** /Design END ****/

}